<template>
  <div class="container paulgrahamcalc">
    <h2>{{ msg }}</h2>
    <p>See <a href="http://paulgraham.com/equity.html">http://paulgraham.com/equity.html</a> for reference.</p>
    <!-- do this https://jsfiddle.net/mani04/bgzhw68m/ -->
    <div align="left">
      <p>
        <formatted-input2 formatter="toKMB" label="At a company valued at" min=1 v-model.number="valuation"></formatted-input2>
        <formatted-input2 formatter="toKMB" label=" with a salary of " v-model.number="salary"></formatted-input2>
        <formatted-input2 formatter="toPercent" label=" vested equity of " v-model.number="equity"></formatted-input2>
        <formatted-input2 formatter="toPercent" label=" salary overhead factor of " v-model.number="overhead_factor" min=0 type="number"></formatted-input2>
        <formatted-input2 formatter="toPercent" label=" and profit margin of " v-model.number="profit_margin" min=0 type="number"></formatted-input2>
        the individuals "equitized salary" is <b>{{ equitized_salary | toPercent(3) }}</b>
        for a total equity of roughly <b>{{ total_equity | toPercent(3) }}</b> of the value of the company.
      </p>
      <p v-if="implied_alpha>= 0 ">
        The individual has an implied total equity of <b>{{ implied_total_equity | toPercent(3) }}</b> 
        and their implied alpha is priced to be <b>{{ implied_alpha | toPercent(3) }}</b> by the offer.
      </p>
      <p v-else>
        The individual has an implied total equity of <b>{{ implied_total_equity | toPercent(3) }}</b>,
        the firm effectively is in negative equity.
        The implied alpha is negative so the Graham calculation breaks down in its original form.
        Consider a linear (shift) instead of multiplicative contribution might make sense.
      </p>
      <b>This means that the company expects hiring the individual to at least {{ implied_alpha + 1 | toFixed(2) }}X the company value in the long run</b>
    </div>
  </div>
</template>

<script>

// import FormattedInput from './FormattedInput.vue';
import FormattedInput2 from './FormattedInput2.vue';
export default {
  name : "PaulGrahamCalc",
  components: {
    // FormattedInput,
    FormattedInput2
  },
  props: {
    msg: String
  },
  data () {
    return {
      // alpha: 0.2, // x factor
      equity: 0.005, // (formerly theta) package equity
      valuation: 10000000, // 
      profit_margin: 0.50, // for company on equity/salary deal
      salary: 60000, // 
      overhead_factor: 0.5
    }
  },
  computed: {
    equitized_salary: function() { return this.overhead_factor * this.salary / this.valuation },
    total_equity: function() {
      return this.equitized_salary + this.equity
    },
    implied_total_equity: function() {
      // this is your theoretical fair value supposing we know they company want make a theoretical "profit" on hiring
      return this.total_equity * (1 + this.profit_margin)
    },
    implied_alpha: function() {
      // really a lower bound
      return this.implied_total_equity / (1 - this.implied_total_equity)
    }
  },
  filters: {
    sum: function(arr) {
        return arr.reduce((x, y) => x + y)
    },
    toKMB: function() {
        return {
          read: function(num) {
            return num > 999999 ? (num/1000000).toFixed(1) + 'M' : (num > 999 ? (num/1000).toFixed(1) + 'K' : num)
          },
          write: function(newval) {
            return newval
          }
        }
    },
    toFixed: function(value, precision=2) { return parseFloat(value).toFixed(precision) },
    toPercent: function(value, precision=0) { return parseFloat(value * 100).toFixed(precision) + "%"}
  }
}
</script>

