<template>
    <div>
      <label style="margin-right:0.25rem" v-if="label">{{ label }}</label>
      <input
        style="border:none;font-weight: bold;"
        ref="input"
        v-bind:value="value"
        v-on:input="updateValue($event.target.value)"
        v-on:focus="selectAll"
        v-on:blur="formatValue"
      >
    </div>
</template>
<script>
var formatters = {
    toKMB: function(num) {
      return num > 999999 ? (num/1000000).toFixed(1) + 'M' : (num > 999 ? (num/1000).toFixed(1) + 'K' : num)
    },
    toFixed: function(value) { return parseFloat(value).toFixed(2) },
    toPercent: function(value) { return parseFloat(value * 100).toFixed(1) + "%"}
}
export default {
  name: "FormattedInput2",
  props: {
    value: {
      type: Number,
      default: 0
    },
    label: {
      type: String,
      default: ''
    },
    formatter: {
      type: String,
      default: "toKMB"
    }
  },
  mounted: function () {
    this.formatValue()
  },
  methods: {
    updateValue: function (value) {
      // this.$refs.input.value = value
      this.$emit('input', value)
    },
    formatValue: function () {
      // this.$refs.input.value = "HERE " + this.value // currencyValidator.format(this.value)
      this.$refs.input.value = formatters[this.formatter](this.value) // currencyValidator.format(this.value)
    },
    selectAll: function (event) {
      this.$refs.input.value = this.value // currencyValidator.format(this.value)
      // Workaround for Safari bug
      // http://stackoverflow.com/questions/1269722/selecting-text-on-focus-using-jquery-not-working-in-safari-and-chrome
      setTimeout(function () {
        event.target.select()
      }, 0)
    }
  }
}
</script>
